
import { h, Component } from 'preact';
import { connect } from 'preact-redux';
import toast from '../silly-toast/lib/silly-toast.js';

import './toast-demo.less';

export default class ModalDemo extends Component {

  toast() {
    toast.info('We have already got your information');
  }

  render() {
    return (
      <div>
        <div className="content text-center">
          <h2 className="text-center">Silly Toast</h2>
          <p className="slogan">A simple javascript toast</p>
          <br/>
          <div>
            <input type="text" placeholder="Email" value="hello@world.com"/>
          </div>
          <br/>
          <div>
            <input type="Password" placeholder="Password" value="123456"/>
          </div>
          <br/>
          <button type="button" onClick={this.toast}>
            submit
          </button>
        </div>
      </div>
      );

  }
}



