'use strict';

const styles = require('./styles');
const isServer = typeof window === 'undefined';

let $body;
let winWidth;
let winHeight;
let w;
let d;

// 
if (isServer) {
  $body = { };
  winWidth = 0;
  winHeight = 0;
} else {
  w = window;
  d = document;
  $body = d.body;
  winWidth = w.innerWidth || d.documentnode.clientWidth || d.body.clientWidth;
  winHeight = w.innerHeight || d.documentnode.clientHeight || d.body.clientHeight;
}


/**
 * 
 * const toast = require('toast')
 * toast.info('hello')
 */
module.exports = {
  success(msg, opt = { }) {
    toast('success', msg, opt);
  },
  info(msg, opt = { }) {
    toast('info', msg, opt);
  },
  warning(msg, opt = { }) {
    toast('warning', msg, opt);
  },
  error(msg, opt = { }) {
    toast('error', msg, opt);
  },
};

// private
function toast(type, msg, opt) {
  if (isServer) {
    return;
  }
  const className = opt.className || '';
  const delay = opt.delay || 2000;
  const str = `<div class="toast ${type} ${className}">${msg}</div>`;
  const $toast = createElement(str);

  $body.appendChild($toast);

  setStyle($toast);
  setPosition($toast);

  setTimeout(() => {
    $body.removeChild($toast);
  }, delay);
}

function createElement(string) {
  const div = document.createElement('div');
  div.innerHTML = string;
  return div.firstElementChild;
}

function setPosition(elem) {
  const rectToast = elem.getBoundingClientRect();
  const top = winHeight / 2 - rectToast.height / 2;
  const left = winWidth / 2 - rectToast.width / 2;
  elem.style.top = top + 'px';
  elem.style.left = left + 'px';
}

function setStyle(elem) {
  Object.keys(styles).forEach(key => {
    elem.style[ key ] = styles[ key ];
  });
}



